        },
        {
          id: 3,
          img: "https://i0.wp.com/tvax2.sinaimg.cn/large/006D2oSpgy1h182edrhlhj31hc0ge10u.jpg",
        },
      ],
    };
  },
  watch: {
    screenWidth: {
      handler: function (val) {
        if (val <= 1000) {
          // console.log(val + "屏幕宽度小于900px");
          this.PC = false;
        } else {
          // console.log(val + "屏幕宽度大于900px");
          this.PC = true;
        }
      },
    },
  },
  mounted() {
    this.icon();
    this.sjstd(4);
    this.screenWidth = document.body.clientWidth;

    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
      })();
    };
  },
  methods: {
    sjstd(){
      this.$axios
        .get("/sjs", {    //127.0.0.1是本地服务器     3001是端口号
            params: {
              num:4
          },
        })
        // 在后台查询信息 返回res结果
        .then((res) => {

          if (res.data.code != 200) {
            // 提示信息
            return this.$toast({    // toast是vant组件（要安装和引入才能提示）
              message: "获取设计师失败",
              icon: "fail",
            });
          }
          // console.log(res.data.data);   //  打印后台传给前台的数据
          // 查询成功,把结果赋给icon_arr
          this.sjs = res.data.data;
        }).catch((error)=>{
          //报错
      	});
    },
    icon() {
      this.$axios
        .get("/jzal", {    //127.0.0.1是本地服务器     3001是端口号
        })
        // 在后台查询信息 返回res结果
        .then((res) => {

          if (res.data.code != 200) {
            // 提示信息
            return this.$toast({    // toast是vant组件（要安装和引入才能提示）
              message: "获取家庭案例失败",
              icon: "fail",
            });
          }
          // console.log(res.data.data);   //  打印后台传给前台的数据
          // 查询成功,把结果赋给icon_arr
          this.tp = res.data.data;
        }).catch((error)=>{
          //报错
      	});
    },
  },
};
</script>

