<template>
  <div class="B_lbt">
    <el-carousel
      trigger="click"
      :interval="5000"
      arrow="always"
      :height="PC == true ? height1 : height2"
      style="width:100%;"
    >
      <el-carousel-item v-for="item in lbt" :key="item.id">
        <img referrer="no-referrer" :class="PC == true ? 'pc' : 'h5'" :src="item.img" />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: "B_lbt",
  data() {
    return {
      screenWidth: null,
      height1: "675px",
      height2: "300px",
      PC: false,
      lbt: [
        {
          id: 1,
          img: "https://i0.wp.com/tvax1.sinaimg.cn/large/006D2oSpgy1h182eddiqtj31hc0op46z.jpg",
        },
        {
          id: 2,
          img: "https://i0.wp.com/tvax1.sinaimg.cn/large/006D2oSpgy1h182edk7rtj31hc0opqbm.jpg",
        },
        {
          id: 3,
          img: "https://i0.wp.com/tvax2.sinaimg.cn/large/006D2oSpgy1h182edrhlhj31hc0ge10u.jpg",
        },
      ],
    };
  },
  watch: {
    screenWidth: {
      handler: function (val) {
        if (val <= 1000) {
          // console.log(val + "屏幕宽度小于900px");
          this.PC = false;
        } else {
          // console.log(val + "屏幕宽度大于900px");
          this.PC = true;
        }
      },
    },
  },
  methods: {
    icon() {
      this.$axios
        .get("/lbt", {    //127.0.0.1是本地服务器     3001是端口号
        })
        // 在后台查询信息 返回res结果
        .then((res) => {

          if (res.data.code != 200) {
            // 提示信息
            return this.$toast({    // toast是vant组件（要安装和引入才能提示）
              message: "获取轮播图片失败",
              icon: "fail",
            });
          }
          // console.log(res.data.data);   //  打印后台传给前台的数据
          // 查询成功,把结果赋给icon_arr
          this.lbt = res.data.data;
        }).catch((error)=>{
          //报错
      	});
    },
  },
  mounted() {
    this.icon();
    this.screenWidth = document.body.clientWidth;

    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
      })();
    };
  },
};
</script>

<style lang="scss">
.B_lbt {
  .el-carousel__button {
    width: 20px;
    height: 20px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n + 1) {
    background-color: #d3dce6;
  }
  .pc {
    width: 100%;
    height: 100%;
  }
  .h5 {
    width: 100%;
    height: 100%;
  }
}
</style>